
// runtime
import regeneratorRuntime from 'regenerator-runtime';

// set global
global.regeneratorRuntime = regeneratorRuntime;

// import events
import fs from 'fs-extra';
import polka from 'polka';
import Events from 'events';
import cassandra from 'cassandra-driver';

// local dependencies
import config from './config';

// events
class NFTBackend extends Events {

  /**
   * constructor
   */
  constructor() {
    // run super
    super();

    // run build
    this.building = this.build();
  }

  /**
   * build
   */
  async build() {
    // await database
    await this.__server();
    await this.__database();
  }

  /**
   * setup server
   */
  async __server() {
    // listen
    this.server = polka().listen(9696);
  }

  /**
   * build database
   */
  async __database() {
    return;
    // Log building to debug
    logger.log('debug', 'connecting cassandra', {
      class : 'NFTBackend',
    });

    const db = config.get('db');

    // Replace Username and Password with your cluster settings
    const authProvider = new cassandra.auth.PlainTextAuthProvider(db.username, db.password);

    // ssl options
    const sslOptions = {
      cert               : fs.readFileSync(`${global.appRoot}/AmazonRootCA1.pem`),
      host               : db.hostname,
      rejectUnauthorized : true,
    };

    // Replace PublicIP with the IP addresses of your clusters
    const contactPoints = [db.host];

    // setup client
    this.client = new cassandra.Client({
      contactPoints,
      authProvider,
      sslOptions,

      keyspace        : db.keyspace,
      localDataCenter : db.region,

      pooling : {
        heartBeatInterval        : 15 * 1000,
        maxRequestsPerConnection : 2048 * 24,

        maxConnectionsPerHost : {
          [cassandra.types.distance.local]  : 10 * 4,
          [cassandra.types.distance.remote] : 10 * 4,
        },
        coreConnectionsPerHost : {
          [cassandra.types.distance.local]  : 10 * 4,
          [cassandra.types.distance.remote] : 10 * 4,
        },
      },

      queryOptions : {
        consistency : cassandra.types.consistencies.localQuorum,
      },
    });

    // Log building to debug
    logger.log('debug', 'connected cassandra', {
      class : 'NFTBackend',
    });
  }
}

// export default
const backend = new NFTBackend();

// export default
export default backend;